import { Component, OnInit } from '@angular/core';
import { IonicPage, ViewController } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
/**
 * Generated class for the SelectPrestartItemsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-select-prestart-items',
  templateUrl: 'select-prestart-items.html',
})
export class SelectPrestartItemsPage implements OnInit {
  itemList = [];
  isNoData = false;
  constructor(
    public viewCtrl: ViewController,
    private api: CrudProvider,
    private utiltiy: UtiltiyProvider
    ) {
  }
  ngOnInit() {
  }
  ionViewDidLoad() {
    if(this.viewCtrl.data.list.length){
      this.itemList = this.viewCtrl.data.list;
      return;
    }
    if(this.viewCtrl.data.fleetId){
      this.utiltiy.startLoading();
      let formData: FormData = new FormData();
      formData.append("method_name", "prestart_items");
      formData.append("fleet_no", this.viewCtrl.data.fleetId);
      this.api.post('/prestart/jobcard' , false ,formData ).then((res: any)=>{
        console.log(res);
        if(res.message === 'No Data Found'){
          this.isNoData = true;
          this.utiltiy.stopLoading();
        }
        else{
          this.isNoData  = false;
          res.result[0].vehicles.split(',').forEach((element)=>{
            const data = {
              isChecked: false,
              title: element
            }
            this.itemList.push(data);
          });  
          this.utiltiy.stopLoading();
        }
      }).catch((error)=>{
        console.log(error);
        this.isNoData = true;
        this.utiltiy.stopLoading();
      })
    }else{
      this.isNoData = true;
    }
  }

  closeModal(){
    this.viewCtrl.dismiss(this.itemList)
  }

}



