import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { CabsandTimesheetCreatePage } from '../cabsand-timesheet-create/cabsand-timesheet-create';

@IonicPage()
@Component({
  selector: 'page-cabsand-timesheet-approvals',
  templateUrl: 'cabsand-timesheet-approvals.html',
})
export class CabsandTimesheetApprovalsPage {
  timeSheetList: any = [];
  constructor(public navCtrl: NavController, public navParams: NavParams, private api: CrudProvider, public utiltiy: UtiltiyProvider) {
  }
  ionViewDidLoad() {
    this.gettimeSheetList();
  }

  async gettimeSheetList() {
    this.timeSheetList = [];
    this.utiltiy.startLoading();
    let userId = await this.utiltiy.getStorage("userId");
    let formData: FormData = new FormData();
    formData.append("method_name", "all_approver_list");
    this.api.post(`/tansix_prestart/timesheet/approver`, true, formData)
      .then((result: any) => {
        console.log("result", result);
        this.utiltiy.stopLoading();
        this.timeSheetList = result.result;

      }, err => {
        console.log("err", err);
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      });
  }


  formatAMPM(date) {
    date = date.split(":");
    var hours = date[0];
    var minutes = date[1];
    var ampm = hours >= 12 ? 'PM' : 'AM';
    hours = hours % 12;
    hours = hours ? hours : 12;
    minutes = minutes < 10 ? '0' + minutes : minutes;
    var strTime = hours + ':' + minutes + ' ' + ampm;
    return strTime;
  }

  async acceptRejetc(id) {
    var options = {
      "title": 'Confirmation',
      "message": '',
      "approveText": 'Accept',
      "rejectText": 'Reject',
    };
    let status = await this.utiltiy.attentionDialog(options);
    if (status) {
      this.approveDisaaprove(id, "2", "");
    } else {
      let option = {
        "title": 'Comment',
        "approveText": 'send',
      };
      let comment = await this.utiltiy.inputDialog(option);
      this.approveDisaaprove(id, "4", comment);
      console.log("comment", comment);
    }
    console.log("status", status);
  }

  async approveDisaaprove(id, status, comment) {
    this.utiltiy.startLoading();
    let formData: FormData = new FormData();
    formData.append("timesheet_id", id);
    formData.append("status", status);
    formData.append("comments", comment);
    formData.append("method_name", "approver_timesheet_reject_approve_update_status");
    this.api.post(`/tansix_prestart/timesheet/approver`, true, formData)
      .then((result: any) => {
        console.log("result", result);
        this.utiltiy.stopLoading();
        this.gettimeSheetList();
      }, err => {
        console.log("err", err);
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      });
  }


  updateTimeSheet(prestart){
    this.navCtrl.push(CabsandTimesheetCreatePage, { param: prestart,copy:false });
  }


}



