import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { KlmrJobcardPage } from '../klmr-jobcard/klmr-jobcard';
@IonicPage()
@Component({
  selector: 'page-klmr-prestart-jobcard',
  templateUrl: 'klmr-prestart-jobcard.html',
})
export class KlmrPrestartJobcardPage {
  selectOptions: any;
  params: any;
  fleetId:any;
  payload:any;
  isUpdate: boolean = false;
  jobCardForm: FormGroup;

  requestedList: any = [];
  priorityList: any = [];
  statusList: any = [];
  completedList: any = [];
  jobTypeList: any = [];
  fleetList:any = [];

  commentList:any = [];

  selectedRequest: any = "";
  selectedPriority: any = "";
  selectedStatus: any = "";
  selectedCompletd: any = "";
  selectedJobtype: any = "";
  selectedJob:any = "";
  selectedFleet:any = "";
  constructor(public navCtrl: NavController, public navParams: NavParams, private api: CrudProvider, public utiltiy: UtiltiyProvider, private formBuilder: FormBuilder, private modalController: ModalController) {
    this.selectOptions = {
      mode: 'ios'
    };
    this.params = this.navParams.get('param');
    this.fleetId = this.navParams.get('fleet');
    this.payload = this.navParams.get('payload');
    if (this.params) {
      this.isUpdate = true;
    }
    console.log("this.payload",this.payload);
   
    this.geenerateComments();
    let currentDate = new Date()
    let date = currentDate.toISOString().split('T')[0]
    this.jobCardForm = this.formBuilder.group({
      user_id: new FormControl('', [Validators.required]),
      comment: new FormControl(''),
      job_no: new FormControl('', [Validators.required]),
      plant_item: new FormControl('', [Validators.required]),
      in_date: new FormControl(date, [Validators.required]),
      job_description: new FormControl('', [Validators.required]),
      requested_by: new FormControl(''),
      priority: new FormControl(''),
      notes: new FormControl(''),
      job_status: new FormControl('', [Validators.required]),
      completed_by: new FormControl('', [Validators.required]),
      out_date: new FormControl('', [Validators.required]),
      job_type: new FormControl('', [Validators.required]),
      service_hrs: new FormControl('', [Validators.required]),
      machine_hrs: new FormControl('', [Validators.required]),
      prestart_id: new FormControl('', [Validators.required]),

    });
    this.regenerateForm();

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad KlmrJobcardAddPage');
    this.getMetaData();
  }

  async getMetaData() {
    let userId = await this.utiltiy.getStorage("userId");
    if (userId) {
      this.jobCardForm.get(`user_id`).setValue(userId);
    }
    if (this.fleetId) {
      this.jobCardForm.get(`prestart_id`).setValue("0");
      this.jobCardForm.get(`plant_item`).setValue(this.fleetId);
      this.jobCardForm.get(`job_description`).setValue("Fleet Number "+this.fleetId);
    }
    this.utiltiy.startLoading();
    this.requestedList = await this.getData("requested_list");
    this.priorityList = await this.getData("priority_list");
    this.statusList = await this.getData("status_list");
    this.completedList = await this.getData("completed_list");
    this.jobTypeList = await this.getData("job_type_list");
    if(this.isUpdate){
      this.fleetList = await this.getData("fleet_no_list");
    }
    this.utiltiy.stopLoading();

    // this.locationList = await this.getLocationList();
    if (this.params) {
      this.getComment(this.params.id);
    }
  }
  

  regenerateForm() {
    this.jobCardForm.get(`notes`).setValue(this.payload.notes);
    this.jobCardForm.get(`job_description`).setValue(this.payload.jobDescription);
    this.jobCardForm.get(`plant_item`).setValue(this.payload.fleet_no);
    this.jobCardForm.get(`prestart_id`).setValue(this.payload.id);
    
  }


  geenerateComments(){
    this.payload.fleetList.forEach(element => {
      if(element.status == "Faulty"){
        element.user = this.payload.drivername;
        this.commentList.push(element);
      }
    });
  }




  async getComment(id) {
    let formData: FormData = new FormData();
    formData.append("method_name", "comments");
    formData.append("id", id);
    return this.api.post(`/prestart/jobcard`, false, formData)
    .then((result: any) => {
      console.log("result", result)
      
      if (result.status) {
        this.commentList = result.result
        }
      }, err => {
        console.log("err", err)
        // this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  async getData(methodName) {
    let formData: FormData = new FormData();
    formData.append("method_name", methodName);
    return this.api.post(`/prestart/jobcard`, false, formData)
      .then((result: any) => {
        console.log("result", result)
        if (methodName == "requested_list") {
          result.result.forEach(element => {
            element.name = element.firstName + " " + element.surname;
          });
        }
        if (result.status) {
          return result.result
        }
        return true;
      }, err => {
        console.log("err", err)
        // this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  jobChanged(id) {
    this.utiltiy.startLoading();
    let formData: FormData = new FormData();
    formData.append("method_name", "job_no");
    formData.append("job_no", id);
    this.api.post(`/prestart/jobcard`, false, formData)
      .then((result: any) => {
        console.log("result", result)
        if (result.status) {
        this.jobCardForm.get(`job_no`).setValue(result.result);
        }else{
          this.utiltiy.toaster(result.message);
        }
        this.utiltiy.stopLoading();

      }, err => {
        console.log("err", err)
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  dropDownChange(value, key) {
    this.jobCardForm.get(`${key}`).setValue(value);
  }

  submit() {
    let formKeys = Object.keys(this.jobCardForm.value);
    let formData: FormData = new FormData();
    if (this.isUpdate) {
      formData.append("method_name", "jobcard_update");
      // formData.append("status", "Active");
      formData.append("id", this.params.id);
      formKeys.forEach(key => {
        formData.append(key, this.jobCardForm.value[key]);
      });
      this.jobCard(formData);
    } else {
      formData.append("method_name", "jobcard_store_data");
      // formData.append("status", "Active");
      formKeys.forEach(key => {
        formData.append(key, this.jobCardForm.value[key]);
      });
      this.jobCard(formData);
    }
  }

  
  jobCard(formData){
    this.utiltiy.startLoading();
    this.api.post(`/prestart/jobcard`, false, formData)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.navCtrl.setRoot(KlmrJobcardPage);
        }else{
          this.utiltiy.toaster(result.message);
        }

      }, err => {
        console.log("err", err)
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }



}




