import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { CabsandJobcardAddPage } from '../cabsand-jobcard-add/cabsand-jobcard-add';

@IonicPage()
@Component({
  selector: 'page-cabsand-jobcard',
  templateUrl: 'cabsand-jobcard.html',
})
export class CabsandJobcardPage {
  fleetList:any = [];
  jobCardList:any = [];
  constructor(public navCtrl: NavController, public navParams: NavParams, private api: CrudProvider, public utiltiy: UtiltiyProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad KlmrJobcardPage');
    this.getData();
  }

  async getData(){
    this.fleetList = await this.getFleetList();
    this.jobCardList = await this.getJobcardList();
  }

  async getFleetList() {
    this.fleetList = [];
    this.utiltiy.startLoading();
    let formData: FormData = new FormData();
    formData.append("method_name", "fleet_no_list");
    return this.api.post("/tansix_prestart/jobcard", false,formData)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          var fleetList = [];
          result.result.forEach(element => {
            fleetList.push({
              type: 'radio',
              label: element.fleet_no,
              value: element.fleet_no
            })
          });
          
          return fleetList;
        }
      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
        return [];
      })
  }

  async getJobcardList() {
    let formData: FormData = new FormData();
    formData.append("method_name", "jobcard_list");
    return this.api.post(`/tansix_prestart/jobcard`, false, formData)
      .then((result: any) => {
        console.log("result", result)
        if (result.status) {
          return result.result
        }
        return [];
      }, err => {
        console.log("err", err)
        // this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  async fleetListPopup(){
   let status = await this.utiltiy.fleetDialog({"title":"Select Fleet","data":this.fleetList});
   console.log(status);
   if(status){
    this.cheeckFleetNo(status);
   }
  }

  cheeckFleetNo(fleetNo){
    this.utiltiy.startLoading();
    let formData: FormData = new FormData();
      formData.append("method_name", "check_fleet");
      formData.append("plant_item", fleetNo);
    this.api.post(`/tansix_prestart/jobcard`, false, formData)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.addJobcard(fleetNo);
        }else{
          this.utiltiy.toaster(result.message);
        }

      }, err => {
        console.log("err", err)
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  async deleteRecord(id) {
    let status = await this.utiltiy.showDialog();
    if (status) {
      this.deleteJobcard(id);
    }
    console.log("status", status);
  }

  deleteJobcard(id) {
    this.utiltiy.startLoading();
    let formData: FormData = new FormData();
      formData.append("method_name", "jobcard_delete");
      formData.append("id", id);
    this.api.post(`/tansix_prestart/jobcard`, false, formData)
      .then((result: any) => {
        this.utiltiy.stopLoading();
        if (result.status) {
           this.getData();
        }else{
          this.utiltiy.toaster(result.message);
        }

      }, err => {
        console.log("err", err)
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  addJobcard(fleetNumber) {
    this.navCtrl.push(CabsandJobcardAddPage, { param: null,fleet:fleetNumber });
  }
  updateJobcard(payload) {
    this.navCtrl.push(CabsandJobcardAddPage, { param: payload,fleet:payload.plant_item });
  }

}



