import { Injectable } from '@angular/core';
import { LoadingController, Loading, ToastController, LoadingOptions, AlertController, Platform } from 'ionic-angular';
import { Storage } from '@ionic/storage';

@Injectable()
export class UtiltiyProvider {
  loading: Loading;
  constructor(private loadingCtrl: LoadingController, private toastCtrl: ToastController, private storage: Storage, private alertCtrl: AlertController) {

  }

  startLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent',
      cssClass: 'loader'
    });
    this.loading.present();
  }
  // For stopLoading
  stopLoading() {
    this.loading.dismiss().catch(() => this.loading.dismiss());
  }

  // show toast
  toaster(text) {
    const toast = this.toastCtrl.create({
      message: text,
      duration: 3000,
      position: 'bottom'
    });
    toast.present();
  }

  // For set data inside the local Storage
  setStorage(key, value) {
    this.storage.set(key, value);

  }
  // For get data from local Storage
  async getStorage(key) {
    return this.storage.get(key).then((val) => {
      console.log("value is", val)
      return val
    })
  }

  removeStorage(key) {
    this.storage.remove(key);
  }

  getBase64(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
  }


  async showDialog() {
    return new Promise(async (resolve) => {

      const confirm = await this.alertCtrl.create({
        title: 'Delete',
        message: 'Do you want to delete this record',
        buttons: [
          {
            text: 'No',
            handler: () => {
              console.log('Disagree clicked');
              return resolve(false);

            }
          },
          {
            text: 'Yes',
            handler: () => {
              console.log('Agree clicked');
              return resolve(true);

            }
          }
        ]
      });
      await confirm.present();
    });
  }

  async attentionDialog(options) {
    return new Promise(async (resolve) => {

      const confirm = await this.alertCtrl.create({
        title: options.title,
        message: options.message,
        buttons: [
          {
            text: options.approveText,
            cssClass: 'approveCss',
            handler: () => {
              console.log('Agree clicked');
              return resolve(true);
              
            }
          },
          {
            text: options.rejectText,
            cssClass: 'rejectCss',
            handler: () => {
              console.log('Disagree clicked');
              return resolve(false);
  
            }
          },
        ]
      });
      await confirm.present();
    });
  }

  async fleetDialog(options) {
    return new Promise(async (resolve) => {

      const confirm = await this.alertCtrl.create({
        title: options.title,
        inputs: options.data,
        buttons: [
          {
            text: "OK",
            cssClass: 'approveCss',
            handler: data => {
              console.log('OK clicked. Data -> ' + JSON.stringify(data));
              return resolve(data);
              }

          },
          {
            text: "Cancel",
            cssClass: 'rejectCss',
            handler: () => {
              console.log('Disagree clicked');
              return resolve(false);
  
            }
          },
        ]
      });
      await confirm.present();
    });
  }

  async inputDialog(options) {
    return new Promise(async (resolve) => {

      const confirm = await this.alertCtrl.create({
        enableBackdropDismiss: false ,
        title: options.title,
        inputs: [
          {
            name: 'comments',
            placeholder: 'Comment'
          },
        ],
        buttons: [
          {
            text: options.approveText,
            handler: (data) => {
              console.log('Agree clicked',data.comments);
              if(data.comments.length == 0){
                this.toaster("Comment required");
                return false;
              }else{
                return resolve(data.comments);
              }
              
            }
          },
          
        ]
      });
      await confirm.present();
    });
  }


}



