import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, ViewController } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { SearchPage } from '../search/search';
import { CabsandTimesheetCreatePage } from '../cabsand-timesheet-create/cabsand-timesheet-create';

@IonicPage()
@Component({
  selector: 'page-cabsand-timesheet-create-add',
  templateUrl: 'cabsand-timesheet-create-add.html',
})
export class CabsandTimesheetCreateAddPage {
  createTimeSheet: FormGroup;
  selectOptions: any;
  jobNumber: any = "";
  activityCode: any = "";
  timeDifference: any;
  jobNumberList: any = [];
  list: any = [];
  actiivityCodeList: any = [];
  isUpdate: boolean = false;
  params: any = {};
  timeLoop: any;
  isCopy: boolean = false;
  jobCardData: boolean = false;
  constructor(public navCtrl: NavController, public navParams: NavParams, private api: CrudProvider, public utiltiy: UtiltiyProvider, private formBuilder: FormBuilder, private modalController: ModalController, private viewController: ViewController) {
    this.selectOptions = {
      mode: 'ios'
    };
    this.params = this.navParams.get('param');
    this.isCopy = this.navParams.get('copy');
    if (this.params) {
      this.isUpdate = true;

    }

    let currentDate = new Date();
    let date = currentDate.toISOString().split('T')[0];
    this.createTimeSheet = this.formBuilder.group({
      date: new FormControl(date, [Validators.required]),
      job_no: new FormControl('', [Validators.required]),
      activity_code: new FormControl(''),
      job_description: new FormControl('', [Validators.required]),
      start_time: new FormControl('', [Validators.required]),
      finish_time: new FormControl('', [Validators.required]),
      // activity_type: new FormControl('', [Validators.required]),
      notes: new FormControl('', [Validators.required]),
      user_id: new FormControl(''),
      smoko: new FormControl(false),
      lunch: new FormControl(false),
      // meal: new FormControl(false),
    });
  }

  ionViewDidLoad() {
    this.getMetaData();
    this.timeLoop = setInterval(() => {
      var elements = document.getElementsByClassName("select-text");
      // console.log(elements.length,'-',elements);
      if (elements.length > 0) {
        if (elements[0].innerHTML.length != 0) {
          elements[0].innerHTML = elements[0].innerHTML.split("/")[0];
        }
      }
    }, 500);

  }
  ionViewDidLeave() {
    clearInterval(this.timeLoop);
  }

  async getMetaData() {
    let userId = await this.utiltiy.getStorage("userId");
    if (userId) {
      this.createTimeSheet.get(`user_id`).setValue(userId);
    }
    this.list = await this.getJobNumberList();
    // this.actiivityCodeList = await this.getActivityCodeList();
    if (this.params) {
      if (userId) {
        this.params.user_id = userId;
      }
      this.regenerateForm();
    }
  }

  async getJobNumberList() {
    this.jobNumberList = [];
    this.utiltiy.startLoading();
    return this.api.get("/prestart/job_no/Job_no_get?folder_name=2", false)
      .then((result: any) => {
        console.log("result", result);
        this.utiltiy.stopLoading();
        if (result.status) {
          return result.result;
        }
      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
        return [];
      });
  }

  async getActivityCodeList() {
    this.actiivityCodeList = [];
    this.utiltiy.startLoading();
    return this.api.get("/prestart/activity/get", false)
      .then((result: any) => {
        console.log("result", result);
        this.utiltiy.stopLoading();
        if (result.status) {
          return result.result;
        }
      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
        return [];
      });
  }

  regenerateForm() {
    this.createTimeSheet.patchValue({
      date: this.params.date,
      job_no: this.params.job_no,
      activity_code: this.params.activity_code,
      job_description: this.params.job_description,
      start_time: this.params.start_time,
      finish_time: this.params.finish_time,
      // activity_type: this.params.activity_type,
      notes: this.params.notes,
      user_id: this.params.user_id,
      smoko: this.params.smoko == "false" ? false : true,
      lunch: this.params.lunch == "false" ? false : true,
      meal: this.params.meal == "false" ? false : true,

    });
    this.jobNumber = this.params.job_no;
    this.activityCode = this.params.activity_code;
    this.timeChanged();
  }

  endTimeLimiter() {

  }

  async timeChanged() {
    // this.ampmToHours("01:00 PM")
    console.log(this.createTimeSheet.value.finish_time, "time is", this.createTimeSheet.value.start_time);
    if (this.createTimeSheet.value.finish_time && this.createTimeSheet.value.start_time) {

      this.timeDifference = await this.calculateTimeDifference(this.createTimeSheet.value.start_time, this.createTimeSheet.value.finish_time);
      // if (this.createTimeSheet.value.activity_type.length > 0) {
      // this.timeDifference = await this.deductMinutes(this.timeDifference, 5)
      // }
    }
  }


  calculateTimeDifference(startTime, endTime) {
    var start = startTime.split(":");
    var end = endTime.split(":");
    var startDate = new Date(0, 0, 0, start[0], start[1], 0);
    var endDate = new Date(0, 0, 0, end[0], end[1], 0);
    var diff = endDate.getTime() - startDate.getTime();
    var hours = Math.floor(diff / 1000 / 60 / 60);
    diff -= hours * 1000 * 60 * 60;
    var minutes = Math.floor(diff / 1000 / 60);

    if (hours < 0)
      hours = hours + 24;
    return (hours <= 9 ? "0" : "") + hours + ":" + (minutes <= 9 ? "0" : "") + minutes;
  }

  async deductMinutes(time, difference) {
    var minutesToAdd = difference;
    var currentDate = new Date();
    currentDate.setHours(parseInt(time.split(":")[0]), parseInt(time.split(":")[1]), 0, 0);
    var futureDate = new Date(currentDate.getTime() - minutesToAdd * 60000);
    return (futureDate.getHours() <= 9 ? "0" : "") + futureDate.getHours() + ":" + (futureDate.getMinutes() <= 9 ? "0" : "") + futureDate.getMinutes();
  }


  formatAMPM(date) {
    var hours = date[0];
    var minutes = date[1];
    var ampm = hours >= 12 ? 'PM' : 'AM';
    hours = hours % 12;
    hours = hours ? hours : 12;
    minutes = minutes < 10 ? '0' + minutes : minutes;
    var strTime = hours + ':' + minutes + ' ' + ampm;
    return strTime;
  }

  ampmToHours(time) {
    var hours = Number(time.match(/^(\d+)/)[1]);
    var minutes = Number(time.match(/:(\d+)/)[1]);
    var AMPM = time.match(/\s(.*)$/)[1];
    if (AMPM === "PM" && hours < 12) hours = hours + 12;
    if (AMPM === "AM" && hours === 12) hours = hours - 12;
    var sHours = hours.toString();
    var sMinutes = minutes.toString();
    if (hours < 10) sHours = "0" + sHours;
    if (minutes < 10) sMinutes = "0" + sMinutes;
    console.log("--", (sHours + ":" + sMinutes));
    return (sHours + ":" + sMinutes);
  }


  add(item) {
    this.jobNumberList = [];
    this.jobNumber = item.job_no;
    this.createTimeSheet.get(`job_no`).setValue(item.job_no);
    this.createTimeSheet.get(`job_description`).setValue(item.job_description);

  }

  removeFocus() {
    console.log("ii");
    this.jobNumberList = [];
  }

  search() {
    if (!this.jobNumber.trim().length) {
      this.jobNumberList = [];
      return;
    }

    this.jobNumberList = this.list.filter(item => item.job_no.toUpperCase().includes(this.jobNumber.toUpperCase()));
  }

  async onOpenCommonModal() {
    // console.log("iii");
    // let modal = this.modalController.create(SearchPage, {data: "data"}, {cssClass: 'select-modal' });

    // let finalList = await this.manupilateList();
    // console.log("finalList",finalList);
    //   let modal =  this.modalController.create(SearchPage, {data: "iii"}, {cssClass: 'search-dialog' });

    //   modal.onDidDismiss( (res) => {

    //     if (res) {
    //       console.log("success")
    //    }});
    //  modal.present();

    //  const modal = await this.modalController.create({
    //   component: SearchPage,
    //   cssClass: 'search-dialog',
    //   backdropDismiss: true,

    // });
    // modal.onDidDismiss( (res) => {

    //       if (res) {
    //         console.log("success")
    //      }});
    // return await modal.present();


  }


  async manupilateList() {
    let manupilatedList = [];
    this.jobNumberList.forEach(element => {
      manupilatedList.push({ 'key': element.job_no, 'value': element.job_no });
    });
    return manupilatedList;
  }




  dropDownChange(value, key) {
    // this.createTimeSheet.get(`${key}`).setValue(value);
    this.createTimeSheet.get(`job_no`).setValue(value);
    // this.createTimeSheet.get(`job_description`).setValue(value.job_description);
    let index = this.list.findIndex(list_ => list_.job_no.toLowerCase() == value.toLowerCase());
    if (index != -1) {
      this.createTimeSheet.get(`job_description`).setValue(this.list[index].job_description);
    }
  }


  submit() {
    // let startHourMinutes = this.createTimeSheet.value.start_time.split(':');
    // this.createTimeSheet.value.start_time = this.formatAMPM(startHourMinutes);
    // let finishHourMinutes = this.createTimeSheet.value.finish_time.split(':');
    // this.createTimeSheet.value.finish_time = this.formatAMPM(finishHourMinutes);

    let formKeys = Object.keys(this.createTimeSheet.value);
    if (this.isCopy) {

      let formData: FormData = new FormData();
      formKeys.forEach(key => {
        if (key == "smoko" || key == "lunch" || key == "meal") {
          var value = this.createTimeSheet.value[key] == false ? "false" : "true";
          formData.append(key, value);
        } else {
          formData.append(key, this.createTimeSheet.value[key]);
        }
      });
      this.addTimeSheet(formData);
    } else {

      if (this.isUpdate) {
        let string = "";
        formKeys.forEach(key => {
          if (string.length == 0) {
            string = string + "" + key + "=" + this.createTimeSheet.value[key];
          } else {
            if (key == "smoko" || key == "lunch" || key == "meal") {
              var value = this.createTimeSheet.value[key] == false ? "false" : "true";
              string = string + "&" + key + "=" + value;
            } else {
              string = string + "&" + key + "=" + this.createTimeSheet.value[key];
            }

          }
        });
        let body = string;
        console.log("string-1", body);
        this.updateTimeSheet(body, this.params.id);
      } else {

        let formData: FormData = new FormData();
        formKeys.forEach(key => {
          formData.append(key, this.createTimeSheet.value[key]);
        });
        this.addTimeSheet(formData);
      }
    }
    console.log("this.credentialsForm.value", this.createTimeSheet.value);


  }

  addTimeSheet(formData) {
    this.api.post(`/tansix_prestart/timesheet/store`, false, formData)
      .then((result: any) => {
        console.log("result", result);
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          if (this.jobCardData) {
            this.viewController.dismiss();
            return;
          }
          this.navCtrl.setRoot(CabsandTimesheetCreatePage);
        }

      }, err => {
        console.log("err", err);
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      });
  }

  updateTimeSheet(formData, id) {
    this.api.put(`/tansix_prestart/timesheet/update/${id}`, false, formData)
      .then((result: any) => {
        console.log("result", result);
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.navCtrl.setRoot(CabsandTimesheetCreatePage);
        }

      }, err => {
        console.log("err", err);
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      });
  }





}




