import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, Events, ActionSheetController, NavController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { FingerprintAIO } from '@ionic-native/fingerprint-aio';


import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';
import { LoginPage } from '../pages/login/login';
import { DashboardPage } from '../pages/dashboard/dashboard';
import { UtiltiyProvider } from '../providers/utiltiy/utiltiy';
import { PrestartPage } from '../pages/prestart/prestart';
import { CabsandPrestartPage } from '../pages/cabsand-prestart/cabsand-prestart';
import { KlmrFuelPage } from '../pages/klmr-fuel/klmr-fuel';
import { KlmrFuelTruckPage } from '../pages/klmr-fuel-truck/klmr-fuel-truck';
import { CabsandFuelPage } from '../pages/cabsand-fuel/cabsand-fuel';
import { CabsandFuelTruckPage } from '../pages/cabsand-fuel-truck/cabsand-fuel-truck';
import { KlmrJsaAddPage } from '../pages/klmr-jsa-add/klmr-jsa-add';
import { KlmrJsaPage } from '../pages/klmr-jsa/klmr-jsa';
import { CabsandJsaPage } from '../pages/cabsand-jsa/cabsand-jsa';
import { KlmrTimesheetCreatePage } from '../pages/klmr-timesheet-create/klmr-timesheet-create';
import { CabsandTimesheetCreatePage } from '../pages/cabsand-timesheet-create/cabsand-timesheet-create';
import { KlmrNewsPage } from '../pages/klmr-news/klmr-news';
import { CabsandNewsPage } from '../pages/cabsand-news/cabsand-news';
import { KlmrTimesheetPendingPage } from '../pages/klmr-timesheet-pending/klmr-timesheet-pending';
import { KlmrTimesheetApprovalsPage } from '../pages/klmr-timesheet-approvals/klmr-timesheet-approvals';
import { KlmrTimesheetHistoryPage } from '../pages/klmr-timesheet-history/klmr-timesheet-history';
import { CabsandTimesheetPendingPage } from '../pages/cabsand-timesheet-pending/cabsand-timesheet-pending';
import { CabsandTimesheetHistoryPage } from '../pages/cabsand-timesheet-history/cabsand-timesheet-history';
import { CabsandTimesheetApprovalsPage } from '../pages/cabsand-timesheet-approvals/cabsand-timesheet-approvals';
import { KlmrJobcardPage } from '../pages/klmr-jobcard/klmr-jobcard';
import { CabsandJobcardPage } from '../pages/cabsand-jobcard/cabsand-jobcard';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any;

  // pages: Array<{ title: string, component: any }>;
  pages: any = [];
  showLevel1 = null;
  showLevel2 = null;
  sideMenu: any = [];
  currentGroup: any = null;
  biometricAvailable: boolean = false;
  biometricCounter:any = 0;
  constructor(public platform: Platform, public actionSheetCtrl: ActionSheetController, public faio: FingerprintAIO, public statusBar: StatusBar, public events: Events, public splashScreen: SplashScreen, private utility: UtiltiyProvider) {
    this.initializeApp();
    events.subscribe('chnageSideMenu', (role) => {
      this.roleBasedSideMenu(role);
    });

    events.subscribe('currentClass', (selectedGroup) => {
      this.changeGroup(selectedGroup);

    });




    // used for an example of ngFor and navigation
    this.sideMenu = [
      {
        "category": "Klmr",
        "colorCode": "#b71464",
        "subs": [
          {
            "title": "Prestart",
            "component": PrestartPage,
          },
          {
            "title": "Fuel",
            "component": KlmrFuelPage,
          },
          {
            "title": "Fuel Truck",
            "component": KlmrFuelTruckPage,
          },
        ]
      },
      {
        "category": "Cabsand",
        "colorCode": "#b1651c",
        "subs": [
          {
            "title": "Prestart",
            "component": CabsandPrestartPage,
          },
        ]
      },
    ]



  }


  async openMenu() {
    let biometricStatus = await this.utility.getStorage("biometric");

    let actionSheet = this.actionSheetCtrl.create({
      title: 'Biometric',
      cssClass: 'action-sheets-basic-page',
      buttons: [
        biometricStatus ? {
          text: 'Deactivate',
          role: 'destructive',
          handler: () => {
            this.activateDeactivateBioMetric(false);
          }
        } :
          {
            text: 'Activate',
            handler: () => {
              this.activateDeactivateBioMetric(true);
            }
          },
      ]
    });
    actionSheet.present();
  }

  activateDeactivateBioMetric(status) {
    this.utility.toaster(status ? "Activated" : "Deactivated");
    this.utility.setStorage("biometric", status);
  }

  async getappColor() {
    // this.appColor = await this.utility.getStorage("primaryColor");
  }


  logout() {
    this.utility.removeStorage("token");
    this.utility.removeStorage("roleID");
    this.utility.removeStorage("primaryColor");
    this.utility.removeStorage("name");
    this.utility.removeStorage("permissions");
    this.utility.removeStorage("biometric");
    this.nav.setRoot(HomePage);
  }

  toggleLevel1(idx) {
    if (this.isLevel1Shown(idx)) {
      this.showLevel1 = null;
    } else {
      this.showLevel1 = idx;
    }
  };

  toggleLevel2(idx) {
    if (this.isLevel2Shown(idx)) {
      this.showLevel1 = null;
      this.showLevel2 = null;
    } else {
      this.showLevel1 = idx;
      this.showLevel2 = idx;
    }
  };

  isLevel1Shown(idx) {
    return this.showLevel1 === idx;
  };

  isLevel2Shown(idx) {
    return this.showLevel2 === idx;
  };

  async getState() {
    console.log("i was called -4");
    let status = await this.utility.getStorage("token");
    let roleId = await this.utility.getStorage("roleID");
    let currentGroup = await this.utility.getStorage("currentClass");
    let biometricStatus = await this.utility.getStorage("biometric");


    if (status) {
      if (biometricStatus) {
        let verifiedUser = await this.verifyUser();
        console.log("verifiedUser", verifiedUser);
        if (!verifiedUser) {
          this.bioLogout();
          return;
        } else {
          this.utility.toaster("Welcome Back");
        }
      }
      // this.roleBasedSideMenu(roleId);
      // this.rootPage = HomePage;
      if (currentGroup == "klmr") {
        this.rootPage = KlmrNewsPage;
      } else if (currentGroup == "cabsand") {
        this.rootPage = CabsandNewsPage;
      }

    } else {
      this.rootPage = HomePage;
    }

    if (currentGroup) {
      this.changeGroup(currentGroup);

    } else {
      this.pages = [
        { title: 'Home', component: HomePage },
      ]
    }
    console.log("status", status)
  }


  async resumeBioMetric() {
    console.log("i was called-1");
    let biometricStatus = await this.utility.getStorage("biometric");
    if (biometricStatus) {
      this.faio.show({
        clientId: 'klmr-cabsand',
        clientSecret: 'klmrcabsand', //Only necessary for Android
        disableBackup: true, //Only for Android(optional)
        localizedFallbackTitle: 'Use Pin', //Only for iOS
        localizedReason: 'Please Authenticate' //Only for iOS
      })
        .then(async (status: any) => {
          console.log("status", status)
          this.utility.toaster("Welcome Back");
          this.biometricCounter++;
        })
        .catch((error: any) => {
          this.bioLogout();
          //Fingerprint/Face was not successfully verified
          //  this.utility.presentAlert(error);

        });
    }
  }

  bioLogout() {
    this.nav.setRoot(LoginPage);
    this.utility.toaster("Invalid User");
    this.utility.removeStorage("token");
    this.utility.removeStorage("roleID");
    this.utility.removeStorage("primaryColor");
    this.utility.removeStorage("name");
    this.utility.removeStorage("permissions");
    this.utility.removeStorage("biometric");
  }

  checkBioMetric() {

    if (this.platform.is('mobileweb') || this.platform.is('core')) {
      // this.biometricAvailable = true;
      return;
    }

    this.faio.isAvailable()
      .then(async (result) => {
        console.log("result----", result)
        if (result === "biometric" || result === "face") {
          this.biometricAvailable = true;
        }
      });
  }

  async verifyUser() {
    console.log("platform is", this.platform);
    if (this.platform.is('mobileweb') || this.platform.is('core')) {
      console.log("it is web");
      // this.biometricAvailable = true;
      return true;
    } else {
      console.log("it is app");

      //Check if Fingerprint or Face  is available
      return this.faio.isAvailable()
        .then(async (result) => {
          console.log("result", result)
          if (result === "biometric" || result === "face") {
            this.biometricAvailable = true;
            //Fingerprint or Face Auth is available
            return this.faio.show({
              clientId: 'klmr-cabsand',
              clientSecret: 'klmrcabsand', //Only necessary for Android
              disableBackup: true, //Only for Android(optional)
              localizedFallbackTitle: 'Use Pin', //Only for iOS
              localizedReason: 'Please Authenticate' //Only for iOS
            })
              .then(async (status: any) => {
                console.log("status", status)
                return true;
              })
              .catch((error: any) => {
                console.log("error is", error)
                //Fingerprint/Face was not successfully verified
                //  this.utility.presentAlert(error);
                return false;

              });
          }
          else {
            return true;

            //Fingerprint or Face Auth is not available
            //  this.utility.presentAlert("Fingerprint/Face Auth is not available   on this device!");
          }
        })
    }
  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
      this.getState();
      this.platform.pause.subscribe(async () => {
        this.biometricCounter = 0;
      });

      this.platform.resume.subscribe(async () => {
        if( this.biometricCounter == 0){
          this.resumeBioMetric();
        }
        //Do stuff here
      });
      this.checkBioMetric();

    });
  }

  changeGroup(group) {
    this.currentGroup = group;
    if (group == "klmr") {
      this.pages = [
        {
          "category": null,
          "subs": [
            {
              "title": "News",
              "component": KlmrNewsPage,
              "pageIdentity": "cabsand_fuel_ViewFuelRO"
            },
          ]
        },
        {
          "category": "Prestart",
          "subs": [
            {
              "title": "Prestart",
              "component": PrestartPage,
              "pageIdentity": "cabsand_fuel_ViewFuelRO"
            },
          ]
        },
        {
          "category": "Jobcard",
          "subs": [
            {
              "title": "Jobcard",
              "component": KlmrJobcardPage,
              "pageIdentity": "jobcard_ViewJobcardRO"
            },
          ]
        },
        {
          "category": "Timesheets",
          "subs": [
            {
              "title": "Create",
              "component": KlmrTimesheetCreatePage,
              "pageIdentity": "timesheet_ViewTimesheetRO"
            },
            // {
            //   "title": "Pending",
            //   "component": KlmrTimesheetPendingPage,
            //   "pageIdentity": "timesheet_ViewPendingRO"
            // },
            {
              "title": "History",
              "component": KlmrTimesheetHistoryPage,
              "pageIdentity": "timesheet_ViewHistoryRO"
            },
            {
              "title": "Approvals",
              "component": KlmrTimesheetApprovalsPage,
              "pageIdentity": "timesheet_ViewApprovalRO"
            },
          ]
        },
        {
          "category": "Fuel",
          "subs": [
            {
              "title": "Fuel Truck",
              "component": KlmrFuelTruckPage,
              "pageIdentity": "cabsand_fuel_ViewFuelRO"
            },
            {
              "title": "Fuel",
              "component": KlmrFuelPage,
              "pageIdentity": "fuel_ViewFuelRO"

            },

          ]
        },
        {
          "category": "Forms",
          "subs": [
            {
              "title": "Jsa",
              "component": KlmrJsaPage,
              "pageIdentity": "jsa_ViewJsaRO"

            },

          ]
        },
      ]

    } else if (group == "cabsand") {
      this.pages = [
        {
          "category": null,
          "subs": [
            {
              "title": "News",
              "component": CabsandNewsPage,
              "pageIdentity": "cabsand_prestart_ViewPrestartRO"

            },
          ]
        },
        {
          "category": "Prestart",
          "subs": [
            {
              "title": "Prestart",
              "component": CabsandPrestartPage,
              "pageIdentity": "cabsand_prestart_ViewPrestartRO"

            },
          ]
        },
        {
          "category": "Jobcard",
          "subs": [
            {
              "title": "Jobcard",
              "component": CabsandJobcardPage,
              "pageIdentity": "cabsand_jobcard_ViewJobcardRO"
            },
          ]
        },

        {
          "category": "Timesheets",
          "subs": [
            {
              "title": "Create",
              "component": CabsandTimesheetCreatePage,
              "pageIdentity": "cabsand_timesheet_EditTimesheetRW"
            },
            // {
            //   "title": "Pending",
            //   "component": CabsandTimesheetPendingPage,
            //   "pageIdentity": "cabsand_timesheet_ViewPendingRO"
            // },
            {
              "title": "History",
              "component": CabsandTimesheetHistoryPage,
              "pageIdentity": "cabsand_timesheet_ViewHistoryRO"
            },
            {
              "title": "Approvals",
              "component": CabsandTimesheetApprovalsPage,
              "pageIdentity": "cabsand_timesheet_ViewApprovalRO"
            },
          ]
        },
        {
          "category": "Fuel",
          "subs": [
            {
              "title": "Fuel Truck",
              "component": CabsandFuelTruckPage,
              "pageIdentity": "cabsand_fuel_truck_ViewFuelTruckRO"
            },
            {
              "title": "Fuel",
              "component": CabsandFuelPage,
              "pageIdentity": "cabsand_fuel_ViewFuelRO"
            },
          ]
        },
        {
          "category": "Forms",
          "subs": [
            {
              "title": "Jsa",
              "component": CabsandJsaPage,
              "pageIdentity": "cabsand_jsa_ViewJsaRO"
            },

          ]
        },
      ]
    } else if (group == "home") {
      this.pages = [
        { title: 'Home', component: HomePage },
      ]
    }

  }

  roleBasedSideMenu(role) {
    if (role == "1") {

    } else {

    }

  }

  async permissions(currentId) {

  }


  async openPage(page) {
    let permissions = await this.utility.getStorage("permissions");
    if (permissions) {
      permissions = JSON.parse(permissions);
    }
    console.log("permissions are ",permissions)
    // this.utility.setStorage("primaryColor",parent.colorCode);
    // this.getappColor();
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
//     this.nav.setRoot(page.component);
// return;
    if (page.component == DashboardPage) {
      return;
    }
    if (permissions.permissions.hasOwnProperty(page.pageIdentity)) {
      this.nav.setRoot(page.component);
    } else {
      this.utility.toaster("Access Restricted");
      this.nav.setRoot(page.component);
    }

  }
}



