import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { CabsandTimesheetCreateAddPage } from '../cabsand-timesheet-create-add/cabsand-timesheet-create-add';

@IonicPage()
@Component({
  selector: 'page-cabsand-timesheet-history',
  templateUrl: 'cabsand-timesheet-history.html',
})
export class CabsandTimesheetHistoryPage {
  timeSheetList: any = [];
  weeklySelectedDate = null;
  dateFilterList = [];
  constructor(public navCtrl: NavController, public navParams: NavParams, private api: CrudProvider, public utiltiy: UtiltiyProvider) {
  }

  ionViewDidLoad() {
    this.gettimeSheetList(null);
  }

  async gettimeSheetList(filter) {
    this.timeSheetList = [];
    this.utiltiy.startLoading();
    let userId = await this.utiltiy.getStorage("userId");
    let formData: FormData = new FormData();
    // formData.append("user_id", userId);
    // formData.append("status", "2");
    formData.append("method_name", "history_list");
    if (filter) {
      formData.append("date_filter", filter);
    }
    this.api.post(`/tansix_prestart/timesheet/approver/Approver`, true, formData)
      .then((result: any) => {
        console.log("result", result);
        this.timeSheetList = result.result;
        if (result.result.length > 0) {
          const temp = result.extra_data.date_filter_list.filter(e => e != '');
          this.dateFilterList = temp;
        }
        else {
          this.utiltiy.toaster(result.message);
        }
        this.utiltiy.stopLoading();

      }, err => {
        console.log("err", err);
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      });
  }
  searchByDate() {
    this.gettimeSheetList(this.weeklySelectedDate);
  }

  formatAMPM(date) {
    date = date.split(":");
    var hours = date[0];
    var minutes = date[1];
    var ampm = hours >= 12 ? 'PM' : 'AM';
    hours = hours % 12;
    hours = hours ? hours : 12;
    minutes = minutes < 10 ? '0' + minutes : minutes;
    var strTime = hours + ':' + minutes + ' ' + ampm;
    return strTime;
  }
  async deleteRecord(id) {
    let status = await this.utiltiy.showDialog();
    if (status) {
      this.deleteTimeSheet(id);
    }
    console.log("status", status);
  }

  deleteTimeSheet(id) {
    this.api.delete(`/tansix_prestart/timesheet/delete/${id}`, false)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.searchByDate();
        }

      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }



  updateTimeSheet(prestart){
    this.navCtrl.push(CabsandTimesheetCreateAddPage, { param: prestart,copy:false });
  }
 
  copyTimeSheet(prestart){
   this.navCtrl.push(CabsandTimesheetCreateAddPage, { param: prestart,copy:true });
 }
 

}




