import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { CabsandTimesheetCreateAddPage } from '../cabsand-timesheet-create-add/cabsand-timesheet-create-add';
@IonicPage()
@Component({
  selector: 'page-cabsand-timesheet-create',
  templateUrl: 'cabsand-timesheet-create.html',
})
export class CabsandTimesheetCreatePage {
  timeSheetList: any = [];
  constructor(public navCtrl: NavController, public navParams: NavParams, private api: CrudProvider, public utiltiy: UtiltiyProvider) {
  }

  ionViewDidLoad() {
    this.gettimeSheetList();
  }



  gettimeSheetList() {
    this.timeSheetList = [];
    this.utiltiy.startLoading();
    let formData: FormData = new FormData();
    formData.append("method_name", "timesheet_list");
    this.api.post("/tansix_prestart/timesheet/approver", true,formData)
      .then((result: any) => {
        console.log("result", result)
        this.timeSheetList = result.result;
        this.utiltiy.stopLoading();
      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }


  async deleteRecord(id) {
    let status = await this.utiltiy.showDialog();
    if (status) {
      this.deleteTimeSheet(id);
    }
    console.log("status", status);
  }

  formatAMPM(date) {
    date = date.split(":");
    var hours = date[0];
    var minutes = date[1];
    var ampm = hours >= 12 ? 'PM' : 'AM';
    hours = hours % 12;
    hours = hours ? hours : 12;
    minutes = minutes < 10 ? minutes : minutes;
    var strTime = hours + ':' + minutes + ' ' + ampm;
    return strTime;
  }


  deleteTimeSheet(id) {
    this.api.delete(`/tansix_prestart/timesheet/delete/${id}`, false)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.gettimeSheetList();
        }

      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  async acceptRecord(id) {
    var options = {
      "title": '',
      "message": 'Are you sure?',
      "approveText": 'Yes',
      "rejectText": 'No',
    }
    let status = await this.utiltiy.attentionDialog(options);
    if (status) {
      this.acceptTimesheet(id);
    }
    console.log("status", status);
  }

  async acceptTimesheet(id){
    let userId = await this.utiltiy.getStorage("userId");
    let formData: FormData = new FormData();
    formData.append("method_name", "approver_timesheet_reject_approve_update_status");
    // formData.append("user_id", userId);
    formData.append("timesheet_id", id);
    formData.append("status", '1');
    // timesheet/approver
    this.api.post(`/tansix_prestart/timesheet/approver`, true, formData)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.gettimeSheetList();
        }

      }, err => {
        console.log("err", err)
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

 
  
  addTimeSheet(){
   this.navCtrl.push(CabsandTimesheetCreateAddPage, { param: null,copy:false });
 }
 updateTimeSheet(prestart){
   this.navCtrl.push(CabsandTimesheetCreateAddPage, { param: prestart,copy:false });
 }

 copyTimeSheet(prestart){
  this.navCtrl.push(CabsandTimesheetCreateAddPage, { param: prestart,copy:true });
}

}



